<template>
  <div class="mint-msgbox-wrapper2">
    <transition name="msgbox-bounce">
      <div class="mint-msgbox hc-message-box" :class="{'hc-padding0':!(showCancelButton||showConfirmButton)}" v-show="value">
        <div class="hc-message">
          <svg class="hc-message-icon icon" aria-hidden="true">
            <use :xlink:href="messageIcon"></use>
          </svg>
          <div v-html="message"></div>
          <div class="hc-message-close" @click="handleAction('cancel')">
            <svg class="icon" aria-hidden="true">
              <use xlink:href="#icon-lujing14"></use>
            </svg>
          </div>
        </div>
        <div v-if="showCancelButton||showConfirmButton" class="hc-message-btn">
          <button v-if="showCancelButton" class="hc-btn-cancel" @click="handleAction('cancel')">{{cancelButtonText}}</button>
          <button v-if="showConfirmButton" @click="handleAction('confirm')">{{confirmButtonText}}</button>
        </div>
      </div>
    </transition>
  </div>
</template>

<style scoped>
.hc-padding0 {
  padding: 0px !important;
}
.hc-message-box {
  border-radius: 10px;
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding-bottom: 58px;
}
.hc-message {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.hc-message .hc-message-icon {
  font-size: 30px;
  vertical-align: middle;
  margin-right: 10px;
}
.hc-message div {
  font-size: 16px;
  vertical-align: middle;
}
.hc-message-close {
  position: absolute;
  padding: 14px;
  right: 0px;
  top: 0px;
}
.hc-message-btn {
  height: 58px;
  position: absolute;
  left: 0px;
  bottom: 0px;
  text-align: center;
  width: 100%;
  display: flex;
  border-top: 1px solid #cccccc;
}
.hc-message-btn button {
  line-height: 58px;
  border: none;
  background: none;
  width: 49%;
  font-size: 18px;
  position: relative;
  flex: 1;
  color: #33d98b;
}
.hc-message-btn button:focus {
  border: none;
  box-shadow: none;
  outline: 0;
}
.hc-message-btn button:first-child {
  color: #808080;

}
.hc-btn-cancel::after {
  content: "";
  display: inline-block;
  width: 1px;
  height: 40px;
  right: -2px;
  top: 0px;
  bottom: 0px;
  margin: auto;
  background-color: #cccccc;
  position: absolute;
}

</style>

<script type="text/babel">
  import { Popup } from 'mint-ui';
  export default {
    mixins: [ Popup ],

    props: {
      messageIcon: {
        type: String,
        default: ''
      },
      modal: {
        default: true
      },
    },

    data() {
      return {
        message: '',
        showConfirmButton: true,
        showCancelButton: false,
        confirmButtonText: '',
        cancelButtonText: '',
      };
    },

    methods: {
      doClose() {
        this.value = false;
        this._closing = true;

        this.onClose && this.onClose();

        setTimeout(() => {
          if (this.modal && this.bodyOverflow !== 'hidden') {
            document.body.style.overflow = this.bodyOverflow;
            document.body.style.paddingRight = this.bodyPaddingRight;
          }
          this.bodyOverflow = null;
          this.bodyPaddingRight = null;
        }, 200);
        this.opened = false;

        if (!this.transition) {
          this.doAfterClose();
        }
      },

      handleAction(action) {
        var callback = this.callback;
        this.value = false;
        callback(action);
      },

    },

  };
</script>



